   this.centerDialogVisible = true\n          this.tableData = res.data.data\n        } else {\n          this.centerDialogVisible = false;\n          layer.msg(\"暂无数据\", { time: 500 })\n        }\n      }).catch(err => {\n        throw (err)\n      })\n    },\n    close () {\n      this.tableData = []\n    }\n  }\n}\n</script>\n<style scoped>\n.hoveColor {\n  opacity: 0.5;\n}\n.primary {\n  color: #04bbfc;\n}\n</style>\n"],"sourceRoot":""}]);

